/* example of using mixins */
h1.test {
  -webkit-opacity: 0.5;
  -moz-opacity: 0.5;
  opacity: 0.5;
  text-align: center;
}
.header__container {
  padding: 3.1rem 0rem;
  display: flex;
  justify-content: space-between;
}
.header__container .header__image {
  visibility: hidden;
}
.header__container .icon-add {
  display: none;
}
.header--sticky {
  position: fixed;
  top: 0;
  left: 0;
  box-sizing: border-box;
  z-index: 1;
  background-color: #2A3F50;
  width: 100%;
  box-shadow: 0px 5px 8.3px 1.7px rgba(34, 56, 73, 0.3);
}
.header--sticky .header__image {
  visibility: visible;
}
.header--sticky .header__container {
  padding: 3.1rem 9rem;
}
.header--sticky .icon-add {
  display: inline-block;
}
.header__main-nav {
  display: flex;
  justify-content: flex-end;
  position: relative;
}
.header__image {
  width: 24.2rem;
}
